:host {
  display: grid;
  color: var(--nylas-color-primary-900);
}

.editor {
  position: relative;
  display: block;
  max-height: 300px;
  overflow: auto;

  padding: 1rem;

  border: 1px solid var(--nylas-color-primary-300);

  blockquote {
    visibility: hidden;
    overflow: hidden;
    position: relative;
    transition: height 0.3s ease;
    margin: 0;
  }

  blockquote::before {
    content: '...';
    visibility: visible;
    opacity: 1;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--nylas-color-primary-300);
    width: 20px;
    text-align: center;
    color: var(--nylas-color-primary-800);
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    cursor: pointer;
  }

  .collapsed {
    height: 20px;
  }

  .expanded {
    visibility: visible;
    opacity: 1;
    height: auto; /* Height to fit the content */
    margin: 0px 0px 0px 0.8ex;
    border-left: 1px solid var(--nylas-color-primary-300);
    padding-left: 1rem;
  }

  .expanded::before {
    display: none; /* Hide the '...' when content is expanded */
  }
}

.buttons {
  display: flex;

  margin-top: 1rem;

  justify-content: space-between;

  sp-button {
    border-radius: var(--nylas-border-radius);
    background-color: var(--nylas-color-primary-400);
    color: var(--nylas-color-primary-900);
    fill: var(--nylas-color-primary-900);

    &:hover {
      background-color: var(--nylas-color-primary-500);
    }

    &:disabled {
      background-color: var(--nylas-color-primary-200);
      color: var(--nylas-color-primary-600);
      cursor: not-allowed;
    }

    & chevron-icon {
      display: inline-block;
    }
  }

  .close {
    justify-self: end;
  }
}

.toolbar {
  position: absolute;
  display: none;
  background-color: var(--nylas-color-primary-200);
  border-radius: var(--nylas-border-radius);
  z-index: 1;

  sp-button {
    border: 0;
  }
}
