import { debug } from '@/utils/utils';
import { Message } from '@nylas/core';
import { Component, Event, EventEmitter, Element, Prop, Watch, h, Host } from '@stencil/core';

@Component({
  tag: 'nylas-composer',
  styleUrl: 'nylas-composer.scss',
  shadow: true,
})
export class NylasComposer {
  /**
   * The host element
   */
  @Element() readonly host!: HTMLElement;

  /**
   * The message to reply to or forward
   */
  @Prop() readonly message?: Message;

  /**
   * The view email element
   */
  @Prop() readonly viewEmailElement?: HTMLNylasViewEmailElement | null;

  /**
   * Fired when the composer is closed
   */
  @Event() readonly close!: EventEmitter<MouseEvent>;

  /**
   * Fired when the send button is clicked
   */
  @Event() readonly send!: EventEmitter<{
    message: Message;
    replyBody: string;
  }>;

  /**
   * The editor element
   */
  private editorEl?: HTMLDivElement;

  /**
   * The toolbar element
   */
  private toolbarEl?: HTMLDivElement;

  connectedCallback() {}

  disconnectedCallback() {
    if (this.editorEl) {
      this.editorEl.innerHTML = '';
    }
  }

  componentWillLoad() {}

  async componentDidLoad() {
    if (this.message) {
      this.watchBody(this.message);
    }

    if (!this.editorEl) {
      debug('[connectedCallback] no editor element');
      return;
    }

    if (!this.toolbarEl) {
      debug('[connectedCallback] no toolbar element');
      return;
    }

    const editorEl = this.editorEl;
    const toolbarEl = this.toolbarEl;

    // On highlight, show the toolbar
    editorEl.addEventListener('mouseup', _ => {
      interface ShadowRootWithSelection extends ShadowRoot {
        getSelection(): Selection | null;
      }

      const selection: Selection | null = (this.host.shadowRoot as ShadowRootWithSelection)?.getSelection();
      if (!selection) {
        debug('no selection');
        return;
      }

      // Get range
      const range = selection.getRangeAt(0);

      // Get text selection
      const text = selection.toString();
      if (!text) {
        toolbarEl.style.display = 'none';
        return;
      }

      // Find .message-body in the editor
      const messageBody = editorEl.querySelector('.message-body');
      if (!messageBody) {
        debug('no message body');
        return;
      }

      // Get commonAncestorContainer
      const foundNode = range.commonAncestorContainer;

      // Get rect
      const foundElement = foundNode.parentElement;

      // If we didn't find the text, then don't show the toolbar
      if (!foundElement) {
        return;
      }

      // Get the bounding rect of the message body
      const messageBodyRect = messageBody.getBoundingClientRect();
      debug('messageBodyRect', { messageBodyRect });

      const foundRect = foundElement.getBoundingClientRect();
      debug('foundRect', { foundRect });

      // Find the text position within the found node
      // without using document.createRange()

      // Get the text node
      const textNode = foundElement.childNodes[0];

      // Get the text content
      const textContent = textNode.textContent || '';

      // Get the text position
      const textPosition = textContent.indexOf(text);

      // Calcualte the font width
      const fontSize = window.getComputedStyle(foundElement).fontSize;
      const fontSizeNumber = parseInt(fontSize.replace('px', ''), 10);

      // Top
      const top = foundRect.top - messageBodyRect.top - toolbarEl.clientHeight;
      const left = textPosition * (1 / fontSizeNumber) * 100;

      // Position the toolbar
      toolbarEl.style.top = `${top}px`;
      toolbarEl.style.left = `${left}px`;
      toolbarEl.style.display = 'block';
    });
  }

  @Watch('message')
  watchBody(newMessage: Message) {
    if (!this.editorEl) {
      debug('no editor element');
      return;
    }

    // Find .message-body elements and remove them
    this.editorEl.querySelectorAll('.message-body').forEach(el => el.remove());

    // Create a container for the body of the reply
    const messageBody = document.createElement('div');
    messageBody.classList.add('message-body');

    // Add an empty paragraph to the reply body
    const emptyParagraph = document.createElement('p');
    emptyParagraph.innerHTML = '&nbsp;';
    messageBody.appendChild(emptyParagraph);

    // Also add a break to separate the reply from the rest of the email
    const br = document.createElement('br');
    messageBody.appendChild(br);

    // Append a reply header
    const replyText = document.createElement('div');
    replyText.classList.add('reply-text');
    replyText.innerText = `On ${new Date(newMessage.date * 1000).toLocaleDateString(undefined, { dateStyle: 'full' })} <${newMessage.from
      ?.map(from => from.name || from.email)
      .join(', ')}> wrote:`;
    messageBody.appendChild(replyText);

    // If we have a reply body, separate the contents with a horizontal rule
    if (newMessage.body) {
      // For the reply HTML, we want to wrap the contents in a custom element
      // with a shadow root. This will allow us to style the contents of the
      // reply without affecting the rest of the page.

      // First convert the HTML string to a DOM element
      const replyElement = document.createElement('html');
      replyElement.innerHTML = newMessage.body;

      const replyContainer = document.createElement('blockquote');
      replyContainer.classList.add('collapsed');
      replyContainer.classList.add('gmail_quote');

      // Now add the reply element to the container
      replyContainer.innerHTML = replyElement.innerHTML;

      messageBody.appendChild(replyContainer);

      debug('replyContainer', { replyContainer });
    }

    // Add the message body to the editor
    this.editorEl.appendChild(messageBody);

    this.editorEl.querySelectorAll('blockquote.collapsed').forEach(blockquote => {
      blockquote.addEventListener(
        'click',
        function (event) {
          event.preventDefault();
          const el = event.target as HTMLElement;
          el.classList.remove('collapsed');
          el.classList.add('expanded');
        },
        { once: true },
      );
    });

    // Focus the editor
    this.editorEl.focus();
  }

  private closeComposer = (event: MouseEvent) => {
    this.close.emit(event);
  };

  private sendComposer = (event: MouseEvent) => {
    if (!this.editorEl) {
      debug('no editor element');
      return;
    }

    const messageBody = this.editorEl.querySelector('.message-body');
    if (!messageBody) {
      debug('no message body');
      return;
    }

    // Send the message
    if (this.message) {
      this.send.emit({
        message: this.message,
        replyBody: messageBody.querySelector('.message-body')?.innerHTML || '',
      });

      // Close the composer
      this.close.emit(event);

      // Clear the editor
      this.editorEl.innerHTML = '';
    }
  };

  render() {
    return (
      <Host>
        <sp-theme scale="medium" color="dark">
          <div class="editor" contentEditable={true} ref={r => (this.editorEl = r)}>
            <div class="toolbar" ref={r => (this.toolbarEl = r)} contentEditable={false}>
              <sp-button quiet onClick={() => document.execCommand('bold')}>
                <div slot="icon">
                  <bold-icon></bold-icon>
                </div>
                <div slot="label">Bold</div>
              </sp-button>
              <sp-button quiet onClick={() => document.execCommand('underline')}>
                <div slot="icon">
                  <underline-icon></underline-icon>
                </div>
                <div slot="label">Underline</div>
              </sp-button>
              <sp-button quiet onClick={() => document.execCommand('italic')}>
                <div slot="icon">
                  <italic-icon></italic-icon>
                </div>
                <div slot="label">Italic</div>
              </sp-button>
            </div>
          </div>
          <div class={'buttons'}>
            <sp-button onClick={this.sendComposer} class="send">
              Send
            </sp-button>
            <sp-button onClick={this.closeComposer} class="close">
              Close
            </sp-button>
          </div>
        </sp-theme>
      </Host>
    );
  }
}
